.region-tabs {
  margin-bottom: 20px;
  text-align: center;
}

.region-tabs button {
  margin: 5px;
  padding: 8px 15px;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  background-color: #eee;
  transition: 0.2s;
}

.region-tabs button.active {
  background-color: #ff9800;
  color: white;
}

.region-tabs button:hover {
  background-color: #ffb74d;
  color: white;
}
