.animal-card {
  cursor: pointer;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
  text-align: center;
  transition: transform 0.2s;
  background-color: #fff;
}

.animal-card:hover {
  transform: scale(1.05);
}

.animal-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.animal-card h3 {
  margin: 5px 0;
}

.animal-card p {
  margin: 0;
  font-size: 0.9rem;
  color: #555;
}
