.App {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 0;
  margin: 0;
}

.content {
  padding: 20px;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.search-bar {
  display: block;
  margin: 0 auto 20px auto;
  padding: 10px;
  width: 80%;
  max-width: 400px;
  border-radius: 10px;
  border: 1px solid #ccc;
  font-size: 16px;
}

.animal-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 20px;
}

.about-page {
  text-align: center;
  max-width: 700px;
  margin: 0 auto;
}

/* NavBar */
.navbar {
  display: flex;
  justify-content: center;
  background-color: #4caf50;
  padding: 10px 0;
}

.nav-item {
  color: white;
  margin: 0 15px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

.nav-item:hover,
.nav-item.active {
  color: #ffeb3b;
  transform: scale(1.1);
}

/* RegionTabs */
.region-tabs {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.region-button {
  margin: 5px;
  padding: 8px 12px;
  border: none;
  border-radius: 20px;
  background-color: #f0f0f0;
  cursor: pointer;
  transition: 0.3s;
}

.region-button.active,
.region-button:hover {
  background-color: #4caf50;
  color: white;
}

/* AnimalCard */
.animal-card {
  text-align: center;
  border-radius: 15px;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0px 3px 10px rgba(0,0,0,0.2);
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
}

.animal-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
}

.animal-card h3 {
  margin: 10px 0;
}

/* Modal */
.modal-backdrop {
  position: fixed;
  top:0; left:0;
  width:100%; height:100%;
  background: rgba(0,0,0,0.5);
  display:flex;
  justify-content:center;
  align-items:center;
  z-index: 1000;
}

.modal-content {
  background:white;
  padding:20px;
  border-radius:15px;
  max-width:400px;
  width:90%;
  text-align:center;
  position: relative;
}

.modal-content img {
  width:100%;
  height:200px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
}

.close-button {
  position:absolute;
  top:10px;
  right:10px;
  background:none;
  border:none;
  font-size:20px;
  cursor:pointer;
}
